.answer-items {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.answer-options-buttons {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-line-pack: distribute;
        align-content: space-around;
    margin-top: 25px;
}

.answer_option {
    width: 17%;
    cursor: pointer;
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
    background-color: #246723;
    border: solid 2px #487548;
    margin-top: 20px;
    margin-left: 5px;
    margin-right: 5px;
    padding: 10px;
}

.answer_option:hover {
    background-color: #1a5418;
    border: #3e613e;
    color: #e8e8e8;
    -webkit-transition-property: background-color, border, color;
    -o-transition-property: background-color, border, color;
    transition-property: background-color, border, color;
    -webkit-transition-duration: .5s;
         -o-transition-duration: .5s;
            transition-duration: .5s;
    -webkit-transition-timing-function: cubic-bezier(.36,.29,.6,1.06);
         -o-transition-timing-function: cubic-bezier(.36,.29,.6,1.06);
            transition-timing-function: cubic-bezier(.36,.29,.6,1.06);
}

.answer_option_confirmed {
    width: 17%;
    cursor: pointer;
    margin-top: 20px;
    margin-right: 20px;
    padding: 10px;
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
    background-color: #fb990b;
    border: #3e613e;
    -webkit-transition-property: background-color, border;
    -o-transition-property: background-color, border;
    transition-property: background-color, border;
    -webkit-transition-duration: .5s;
         -o-transition-duration: .5s;
            transition-duration: .5s;
    -webkit-transition-timing-function: cubic-bezier(.36,.29,.6,1.06);
         -o-transition-timing-function: cubic-bezier(.36,.29,.6,1.06);
            transition-timing-function: cubic-bezier(.36,.29,.6,1.06);
}

.answer_option_confirmed:hover {
    border: #3e613e;
    -webkit-transition-property: border;
    -o-transition-property: border;
    transition-property: border;
    -webkit-transition-duration: .5s;
         -o-transition-duration: .5s;
            transition-duration: .5s;
    -webkit-transition-timing-function: cubic-bezier(.36,.29,.6,1.06);
         -o-transition-timing-function: cubic-bezier(.36,.29,.6,1.06);
            transition-timing-function: cubic-bezier(.36,.29,.6,1.06);
}

.example-enter {
    opacity: 0.01;
}
.example-enter-active {
    opacity: 1;
    -webkit-transition: opacity 3000ms ease-in;
    -o-transition: opacity 3000ms ease-in;
    transition: opacity 3000ms ease-in;
}
.example-exit {
    opacity: 1;
}
.example-exit-active {
    opacity: 0.01;
    -webkit-transition: opacity 0ms linear;
    -o-transition: opacity 0ms linear;
    transition: opacity 0ms linear;
}

/*Добавляем вертикальный отступ между кнопками блока при мобильном отображении*/
@media (max-width: 600px) {
    .answer-options-buttons {
        -ms-flex-flow: column wrap;
            flex-flow: column wrap;
        -ms-flex-align: center;
            align-items: center;
        -ms-flex-pack: distribute;
            justify-content: space-around;
        min-height: 100px;
    }
}


