.navigation-buttons {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    -ms-flex-pack: center;
        justify-content: center;
    margin-top: 25px;
    margin-bottom: 15px;
}

.control-button {
    padding: 10px 25px;
    border: none;
    /*Чтобы не было синей рамки при нажатии на кнопку*/
    outline: none;
    color: #ffffff;
    background-color: #fb990b;
    font-size: 14px;
    font-weight: 600;
    font-family: Arial, sans-serif;
    border-radius: 3px;
    cursor: pointer;
    width: 200px;
}

.control-button:hover {
    background-color: #44a543;
    color: #f0f0f0;
    -webkit-transition-property: background-color, color;
    -o-transition-property: background-color, color;
    transition-property: background-color, color;
    -webkit-transition-duration: .5s;
         -o-transition-duration: .5s;
            transition-duration: .5s;
}

.button-previous {
    margin-right: 30px;
}

@media (max-width: 550px) {
    .navigation-buttons {
        -ms-flex-flow: column wrap;
            flex-flow: column wrap;
        -ms-flex-align: center;
            align-items: center;
        min-height: 100px;
        -ms-flex-pack: distribute;
            justify-content: space-around;
    }
    .button-previous {
        margin-right: 0;
    }
    .control-button {
        width: 240px;
    }
}


