.Question-list {
    width: 70%;
    margin: 0 auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.Question-list-item {
    border-radius: 50px;
    background-color: #f7e676;
    font-size: 18px;
    padding: 8px;
    margin-right: 20px;
    cursor: pointer;
    display: -ms-flexbox;
    display: flex;
    width: 21px;
}

.Question-list-item-active {
    background-color: #fb990b;
    border-radius: 50px;
    font-size: 18px;
    padding: 8px;
    margin-right: 20px;
    cursor: pointer;
    display: -ms-flexbox;
    display: flex;
    width: 21px;
    -webkit-transition-property: background-color;
    -o-transition-property: background-color;
    transition-property: background-color;
    -webkit-transition-duration: 1.2s;
         -o-transition-duration: 1.2s;
            transition-duration: 1.2s;
    -webkit-transition-timing-function: cubic-bezier(.24,.63,.36,.94);
         -o-transition-timing-function: cubic-bezier(.24,.63,.36,.94);
            transition-timing-function: cubic-bezier(.24,.63,.36,.94);
}

.Question-list-item-confirmed {
    background-color: #eae9e1;
    border-radius: 50px;
    font-size: 18px;
    padding: 8px;
    margin-right: 20px;
    cursor: pointer;
    display: -ms-flexbox;
    display: flex;
    width: 21px;
}

.question-list-number {
    margin: auto;
}



